import {motion} from 'framer-motion';
import {useState} from 'react';
import {Navbar, Container, Nav} from 'react-bootstrap';
import alaskanHusky from '../images/alaskan-husky.jpg';
import labradorHusky from '../images/labrador-husky.JPG';
import mackenzieHusky from '../images/mackenzie-husky.jpg';
import sakhalinHusky from '../images/sakhalin-husky.jpg';
import siberianHusky from '../images/siberian-husky.jpg';


export const Breeds = () => {

    const [activeLink, setActiveLink] = useState('Alaskan');
    const onUpdateActiveLink = (value) => {
        setActiveLink(value);
    }
    
    return(
    <section className='breeds' id='breeds'>
        <div className='vertical-navbar'>
            <Navbar className='navbar-breeds'>
                <Nav className='flex-column'>
                    <Nav.Link href="#alaskan" className={activeLink === 'alaskan' ? 'active navbar-link-breeds' : 'navbar-link-breeds'} onClick={() => onUpdateActiveLink('alaskan')}>Alaskan</Nav.Link>
                    <Nav.Link href="#labrador" className={activeLink === 'labrador' ? 'active navbar-link-breeds' : 'navbar-link-breeds'} onClick={() => onUpdateActiveLink('labrador')}>Labrador</Nav.Link>
                    <Nav.Link href="#mackenzie" className={activeLink === 'mackenzie' ? 'active navbar-link-breeds' : 'navbar-link-breeds'} onClick={() => onUpdateActiveLink('mackenzie')}>Mackenzie River</Nav.Link>
                    <Nav.Link href="#sakhalin" className={activeLink === 'sakhalin' ? 'active navbar-link-breeds' : 'navbar-link-breeds'} onClick={() => onUpdateActiveLink('sakhalin')}>Sakhalin</Nav.Link>
                    <Nav.Link href="#siberian" className={activeLink === 'siberian' ? 'active navbar-link-breeds' : 'navbar-link-breeds'} onClick={() => onUpdateActiveLink('siberian')}>Siberian</Nav.Link>
                </Nav>
            </Navbar>
        </div>
        <motion.h2 className='title-breeds' initial={{opacity: 0, y: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.2, duration: 0.5 }}} viewport={{once: false, amount: .5}}>Breeds</motion.h2>
        {/* <motion.p className='content' initial={{opacity: 0, y: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.4, duration: 0.5 }}} viewport={{once: false, amount: .5}}>This is a list of dog breeds which contain "husky" in their name. To see a complete list of sled breeds, see Sled dog.</motion.p> */}
        <div className='husky-types'>
            <div className='husky-type' id='alaskan'>
                <motion.h3 initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, x: 0, transition: { delay: 0.2, duration: 0.5 }}} viewport={{once: false, amount: .5}}>Alaskan husky</motion.h3>
                <motion.p className='content' initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.4, duration: 0.5 }}} viewport={{once: false, amount: .5}}>The most commonly used dog in dog sled racing, the Alaskan husky is a mongrel bred specifically for its performance as a sled dog. The modern Alaskan husky reflects 100 years or more of crossbreeding with English Pointers, German Shepherd Dogs, Salukis and other breeds to improve its performance. They typically weigh between 18 and 34 kg (40 and 75 lb) and may have dense or sleek fur. Alaskan huskies bear little resemblance to the typical husky breeds they originated from, or to each other.</motion.p>
                <motion.img className="type-image" src={alaskanHusky} alt='Alaskan Husky'></motion.img>
            </div>
            <div className='husky-type' id='labrador'>
                <motion.h3 initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, x: 0, transition: { delay: 0.2, duration: 0.5 }}} viewport={{once: false, amount: .5}}>Labrador husky</motion.h3>
                <motion.p className='content' initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.4, duration: 0.5 }}} viewport={{once: false, amount: .5}}>The Labrador Husky originated in the Canadian region of Labrador. The breed probably arrived in the area with the Inuit who came to Canada around 1300 AD. Despite the name, Labrador huskies are not related to the Labrador retriever, but in fact most closely related to the Canadian Eskimo Dog. There are estimated to be 50–60 Labrador huskies in the world.</motion.p>
                <motion.img className="type-image" src={labradorHusky} alt='Labrador Husky'></motion.img>
            </div>
            <div className='husky-type' id='mackenzie'>
                <motion.h3 initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, x: 0, transition: { delay: 0.2, duration: 0.5 }}} viewport={{once: false, amount: .5}}>Mackenzie River husky</motion.h3>
                <motion.p className='content' initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.4, duration: 0.5 }}} viewport={{once: false, amount: .5}}>The term Mackenzie River husky describes several overlapping historical populations of Arctic and sub-Arctic sled dog-type dogs, none of which constituted a breed. Dogs from the Yukon Territory were crossed with large European breeds such as St. Bernards and Newfoundlands to create a powerful freighting dog capable of surviving harsh arctic conditions during the Klondike Gold Rush.</motion.p>
                <motion.img className="type-image" src={mackenzieHusky} alt='Mackenzie Husky'></motion.img>
            </div>
            <div className='husky-type' id='sakhalin'>
                <motion.h3 initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, x: 0, transition: { delay: 0.2, duration: 0.5 }}} viewport={{once: false, amount: .5}}>Sakhalin husky</motion.h3>
                <motion.p className='content' initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.4, duration: 0.5 }}} viewport={{once: false, amount: .5}}>The Sakhalin Husky is a critically endangered landrace and sled laika associated with Sakhalin Island and adjacent areas. They are also known Karafuto Ken, Sakhalin Laika, or Gilyak Laika. While bred primarily as a sled dog, Sakhalin Huskies are also used for hunting bear and fishing. There are approximately 20 Sakhalin Huskies remaining on Sakhalin Island.</motion.p>
                <motion.img className="type-image" src={sakhalinHusky} alt='Sakhalin Husky'></motion.img>
            </div>
            <div className='husky-type' id='siberian'>
                <motion.h3 initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, x: 0, transition: { delay: 0.2, duration: 0.5 }}} viewport={{once: false, amount: .5}}>Siberian husky</motion.h3>
                <motion.p className='content' initial={{opacity: 0, x: 50}} whileInView={{opacity: 1, y: 0, transition: { delay: 0.4, duration: 0.5 }}} viewport={{once: false, amount: .5}}>The Siberian Husky is smaller than the similar-appearing Alaskan Malamute. They are descendants of the Chukotka sled dogs bred and used by the native Chukchi people of Siberia, a people of Paleosiberian origin, around the year 2000 BC. Imported to Alaska in the early 1900s, they were used as working dogs and racing sled dogs in Nome, Alaska throughout the 1910s, often dominating the All-Alaska Sweepstakes. They later became widely bred by recreational mushers and show-dog fanciers in the U.S. and Canada as the Siberian Husky, after the popularity garnered from the 1925 serum run to Nome. Siberians stand 20–23.5 inches, weigh between 35 and 60lbs (35-50 for females, 45-60 for males), and have been selectively bred for both appearance and pulling ability. They are still used regularly today as sled dogs by competitive, recreational, and tour-guide mushers.</motion.p>
                <motion.img className="type-image" src={siberianHusky} alt='Siberian Husky'></motion.img>
            </div>
        </div>
    </section>
    )
}